import React, { useState } from "react";
import arrow from "../../assets/arrow-up.svg";

const FaqItem = ({ title, answer, isActive, setActive }) => (
	<div className={`faqItem ${isActive ? "active" : ""}`}>
		<button type="button" onClick={setActive}>
			<span>{title}</span>
			<img src={arrow} />
		</button>
		{isActive && <div>{answer}</div>}
	</div>
);

export default function Faqs() {
	const [activeQuestion, setActiveQuestion] = useState(0);
	const questions = [
		{
			title: "What is ABI workshops?",
			answer:
				"ABI workshops was started in 2018 by Ar. Ankita Kakani \nWe saw the acute gap between the coursework of architecture and the professional world and decided to take a step towards bridging this gap by incorporating practical and engaging methodologies in our workshops. \nAt ABI workshops, we maintain an informal and interactive method of teaching to make the students feel comfortable and involved in the learning process. Our aim is to guide the young aspiring architects to build a strong foundation in their nascent years of learning. \n",
		},
		{
			title: "How do I get details about the workshop?",
			answer:
				"For any question the you may have, you can whatsapp us on +91 9739638321",
		},
		{
			title: "Is this a live session or pre-recorded?",
			answer:
				"Its a completely live and interactive session. The link to join the platform will be sent to you 24 hours prior to the workshop. Provided you have registered.",
		},
		{
			title: "Would there be a certificate of completion?",
			answer:
				"Yes! You will get a certificate of participation on successfully completing the workshop.",
		},
		{
			title:
				"Do I need a particular software installed prior to the workshop?",
			answer:
				"No. You do not require anything pre-installed. We would be using online tools to complete the exercises during the workshop. No homework given.",
		},
		{
			title: "When and how long is the workshop?",
			answer:
				"The workshop will be held over 2 days (weekend). Part1 and Part 2 each of 3.5hours. The workshop will be from 4:pm to 7:30pm IST. Part 1 will focus on all essentials for digital graphic design and Part 2 will focus on implementation of these in your portfolio.",
		},
	];
	const setActive = (id) => {
		console.log(id);
		activeQuestion === id ? setActiveQuestion("") : setActiveQuestion(id);
	};
	return (
		<div className="faqWrap">
			{questions.map((q, i) => (
				<FaqItem
					title={q.title}
					answer={q.answer}
					isActive={activeQuestion === i}
					setActive={() => setActive(i)}
				/>
			))}
		</div>
	);
}
